import { mapMutations} from 'vuex';
export default {
	data() {
		return {
			
		};
	},
	methods:{
		toAddress(){
			uni.navigateTo({url:"/pages/mine/address/address"})
		},
		...mapMutations(['logout']),

		navTo(url){
			this.$api.msg(`跳转到${url}`);
		},
		//退出登录
		toLogout(){
			uni.showModal({
			    content: '确定要退出登录么',
			    success: (e)=>{
			    	if(e.confirm){
			    		this.logout();
			    		setTimeout(()=>{
			    			uni.navigateBack();
			    		}, 200)
			    	}
			    }
			});
		},
		//switch
		switchChange(e){
			let statusTip = e.detail.value ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
